<template>
    <p-data-table
        :value="comments"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        dataKey="id"
        editMode="cell"
    >
        <p-column header="Date" field="signdate" style="width: 120px">
            <template #body="{data}">
                {{
                    data.signdate ? data.signdate.split(" ")[0] : data.signdate
                }}
            </template>
        </p-column>
        <p-column header="Comment" field="comment">
            <template #body="{data}">
                <p-textarea
                    v-model="data.comment"
                    rows="3"
                    style="width: 100%"
                    @focus="onFocus(data)"
                    @input="onCommentInput(data)"
                />
            </template>
        </p-column>
    </p-data-table>
</template>

<script>
import { ref, computed, watch, reactive } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );
        const comments = ref([]);
        const form = reactive({
            lineId: null,
            comment: null
        });

        watch(monitorDetails, () => {
            comments.value = store.state.cycleMonitoring.comments;
        });

        const setSeletedCommnent = (data) => {
            store.commit("cycleMonitoring/SET_SELECTED_COMMMENT", {
                lineId: data.line_id,
                comment: data.comment
            });
        };

        return {
            comments,
            onCommentInput: setSeletedCommnent,
            onFocus: setSeletedCommnent
        };
    }
};
</script>

<style scoped>
.p-inputtextarea {
    font-size: 1rem;
}
</style>
